import util from "../../../common/util";
import Paging from "../../../common/Paging";
import blogSummary from "../common/blogSummary";
import Global from "../common/global";

(() => {

    let allParams = util.pickUpUrlParams();
    let taxonomyId = allParams.get("id");
    if(!taxonomyId){
        location.href = "/Home";
    }
    let paging = new Paging("#paginationContainer", getData);
    const searchsApi = "/api/Essays/EssayType";
    function getData(page: number = 1) {
        let data: any = {
            id: taxonomyId,
            page: page
        };
        Global.showLoading();
        $.ajax({
            type: "get",
            url: searchsApi,
            data: data,
            success: (msg: any) => {
                if(msg.Code !== 0){
                    return;
                }
                var pagingData = msg.Data;
                console.log(pagingData.Essays);
                let taxonomyInfo = `<a class="btn btn-light">${pagingData.Taxonomy.Name}</a> <small style="font-size:16px;">共有<span class="bg-navy pl-1 pr-1">${pagingData.Taxonomy.Num}</span>文章</small>`;        
                $("#taxonomy").html(taxonomyInfo);
                blogSummary.create(pagingData.Essays, '#bIContainer');
                paging.createPagingLi(pagingData.PageIndex, pagingData.TotalPages);
            },
            complete: () => {
                Global.hideLoading();
            }
        })
    }
    
    let page = <string>allParams.get("page");
    if (!page) {
        getData();
    } else {
        getData(parseInt(page));
    }
})();