import $ from 'jquery';
import "../../../common/extensions/global";
import "../../scss/components/_blogSummary.scss";

interface IBlogSummary {
    create: (model: any, selector: string, showSticky?: boolean) => void;
}

const blogSummary: IBlogSummary = (() => {
    const buildSticky = (isSticky: boolean = false, showSticky: boolean = false) => {
        console.log(isSticky, showSticky);
        if (!showSticky) return "";
        if (!isSticky) return "";
        return '<span class="badge badge-success square badge-sticky">置顶</span>';
    }

    var createItem = (item: any, showSticky: boolean = false) => {
        let tags:Array<string> = item.Tags.split('|').map((tag: string) => {
            return `<span class="badge badge-info">${tag}</span>`;
        });
        let itemStr =
            `<div class="blogInfo">
    <div class='b-left'>
        <img src="${item.CoverImg}" />
    </div>
    <div class='b-right'>
        <h3>
            <a href="/Essays/Details/${item.Slug}">${item.Title}</a>${buildSticky(item.Sticky, showSticky)}
        </h3>
        <p>${item.Introduce}</p>
        <div class="sapnContainer">
            <span class="fa fa-bookmark" aria-hidden="true">${item.EssayType}</span>
            <span class="fa fa-tags">
                ${tags.join(" ")}
            </span>
            <span class="fa fa-calendar">${item.Modify.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss")}</span>
            <div class="pull-right">
                <span class="fa fa-eye">${item.ReadNum}</span>
                <span class="fa fa-thumbs-o-up">
                    ${item.Praise}
                </span><span class="fa fa-thumbs-o-down">${item.Criticize}</span>
            </div>
            <div class="clearfix"></div>
        </div>
    </div>
</div>`;

        return itemStr;
    }

    var createSummarys = (blogSummaryArr: Array<any>, showSticky: boolean = false) => {
        let html = blogSummaryArr.map((item: any) => {
            return createItem(item, showSticky);
        }).join(' ');

        return html;
    }


    var create = (data: any, selector: string, showSticky:boolean = false) => {
        var summarys = createSummarys(data, showSticky);
        document.querySelector(selector).innerHTML = summarys;
    }

    return {
        create: create
    }
})();
export default blogSummary;